.popular-services {
  &__blocks {
    grid-template-columns: 1fr 1fr 1fr;

    @media (max-width: 1200px) {
      grid-template-columns: 1fr 1fr;
    }

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
    }
  }

  &__block {
    position: relative;

    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: rgba(255, 255, 255, 0.04);
      border: 2px solid rgba(255, 255, 255, 0.04);
      backdrop-filter: blur(2rem);
    }

    min-height: 428px;

    @media (max-width: 992px) {
      min-height: 0;
    }

    & *:not(.service) {
      z-index: 5;
      position: relative;
    }

    .links-project__link {
      color: var(--bs-black);
      width: fit-content;

      @media (max-width: 768px) {
        font-size: 1rem;
        width: 100%;
      }

      & .service-large-arrow {
        filter: invert(1);
        width: 20px;
        height: 20px;
      }
    }

    &:hover {
      .blur3 {
        opacity: 0.4;
        bottom: -160px;
      }

      .blur4 {
        opacity: 0.25;
        top: -180px;
      }
    }
  }

  &__arrow {
    background: var(--arrow-background);
    border: 1px solid var(--arrow-border);
    box-shadow: inset 0px 0px 0px var(--arrow-box-shadow1),
      0px 2px 4px var(--arrow-box-shadow2);
    border-radius: 50%;
    display: inline;
  }

  &__arrow svg {
    width: 20px;
    height: 20px;
    box-sizing: content-box;
  }
}

.blur3 {
  z-index: 2;
  height: 400px;
  width: 200%;

  background: radial-gradient(
    ellipse at center,
    rgba(70, 66, 243, 0.3) 0%,
    rgba(53, 47, 171, 0.2) 40%,
    rgba(38, 30, 106, 0.05) 71%,
    rgba(18, 7, 21, 0) 100%
  );

  border-radius: 50%;
  transition: all 0.3s ease;

  &.service {
    opacity: 0.3;
    bottom: -180px;
    left: -210px;
  }

  &.helpful {
    width: 80%;
    top: -16%;
    left: 0;
    opacity: 0.2;
  }

  &.review {
    width: 200%;
    top: -250px;
    left: 0;
    opacity: 0.3;
  }
}

.blur4 {
  z-index: 2;
  height: 300px;
  width: 125%;

  background: radial-gradient(
    ellipse at center,
    rgba(70, 66, 243, 0.3) 0%,
    rgba(53, 47, 171, 0.2) 40%,
    rgba(38, 30, 106, 0.05) 71%,
    rgba(18, 7, 21, 0) 100%
  );

  border-radius: 50%;
  transition: all 0.3s ease;

  &.service {
    opacity: 0.15;
    top: -200px;
    left: 0;
  }

  &.helpful {
    width: 90%;
    height: 600px;
    opacity: 0.4;
    bottom: -38%;
    left: 6%;

    @media (max-width: 768px) {
      width: 150%;
      left: -23%;
    }
  }

  &.review {
    opacity: 0.5;
    width: 285%;
    bottom: -250px;
    height: 500px;
    left: -150px;
  }
}
